.top{
    width: 100%;
    height: 50px;
    background-color:white;
    
    /* 메뉴바 고정 */
    position: sticky;   
    top:0;
    /* 반응형 */
    display: flex;  
    justify-content: center;
    
    /* 폰트 */
    font-family: 'Josefin Sans', sans-serif;
}

.container{
   display: flex;
   width: 100%;
   margin-left: 150px;
   margin-right: 150px;
   

    
}

.topLeft{
    flex:0;
    display: flex;
    justify-content: start;
    
   
    
    
}



.topIcon{
    font-size: 30px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
    
}

.topImg{
    width: auto;
    border-radius: 10%;
    object-fit: contain;
   
    
    
}

.topCenter{
    flex:1;
    width: 100%;
    
    align-self: center;
    
}

.topList{
    display: flex;
    width: auto;
    justify-content: space-evenly;
    margin: 0;
    padding: 0;
    list-style: none;
    
}

.topListItem{
    margin-right: 10px;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    
}

.topSearchIcon{
    font-size: 18px;
    color: #444;
    cursor: pointer;
}